import React from 'react'
import { css, cx } from 'linaria'

export interface IframeProps {
  className?: string
  inlineStyles?: { [key: string]: string }
  src: string
  height?: string
  width?: string
  allow?: string
  allowFullScreen?: boolean
  isVideo?: boolean
  title?: string
}

export default (props: IframeProps) => {
  const {
    className,
    inlineStyles,
    src,
    height,
    width,
    allow,
    allowFullScreen,
    isVideo,
    title,
  } = props

  return (
    <div
      className={cx(
        styles.container,
        className,
        isVideo && 'video',
        'iframeComponentBundle'
      )}>
      <iframe
        className="iframe"
        allow={allow}
        src={src}
        style={{ ...inlineStyles }}
        height={height}
        width={width}
        allowFullScreen={allowFullScreen}
        title={title}
      />
    </div>
  )
}

const styles = {
  container: css`
    position: relative;
    display: flex;
    flex: 1 0 auto;
    height: 500px;
    &.video {
      padding-bottom: 56.25%;
      width: 100%;
      height: 100%;
    }
    & > .iframe {
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 100%;
      border: 0;
    }
  `,
}
